import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article5 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo3.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Oświata</h1>
      <h2>
        Historia szkolnictwa w Łomży sięga początków XV wieku, kiedy to założono
        pierwszą parafię, a razem z nią szkołę parafialną. W 1614 roku
        przebywający na terenie miasta jezuici utworzyli Kolegium Jezuickie.
        Jednym z kolejnych rektorów kolegium był św. Andrzej Bobola. Poziom
        szkolnictwa nie spadł po przekazaniu przez KEN szkoły pijarom w 1774
        roku. Pod władzą zaborców w Łomży wykształciło się wiele ważnych
        osobistości, m.in. Szymon Konarski, Rafał Krajewski, Jakub Ignacy Waga,
        Wojciech Szweykowski, Adam Chętnik. Obecnie w Łomży jest dobrze
        rozwinięta sieć szkół publicznych i niepublicznych na wszystkich
        szczeblach. Znajduje się tutaj 7 szkół podstawowych, 8 gimnazjów, 10
        szkół ponadgimnazjalnych, 6 szkół wyższych (w tym 3 niepubliczne) oraz 2
        szkoły artystyczne. Poziom kształcenia w Łomży jest wysoki, o czym
        świadczą wyniki egzaminów maturalnych i gimnazjalnych w skali kraju i
        województwa. Na przykład I Liceum Ogólnokształcące należy do krajowej i
        wojewódzkiej czołówki szkół średnich. Ponadto wiele łomżyńskich
        szkół zdobyło tytuł Szkoły z klasą. Od 1 stycznia 1999 w Łomży swoją
        siedzibę ma Okręgowa Komisja Egzaminacyjna, która obejmuje swoją
        działalnością województwo podlaskie i warmińsko-mazurskie.
      </h2>
    </BackgroundImage>
  )
}

export default Article5
