import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo2.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Informacje Ogólne</h1>
      <h2>
        Łomża to miasto na prawach powiatu w północno-wschodniej Polsce, w
        województwie podlaskim, nad rzeką Narew. Miasto zlokalizowane jest na
        historycznym Mazowszu, stolica dawnej ziemi łomżyńskiej. Łomża to główny
        ośrodek gospodarczy, edukacyjny i kulturowy ziemi łomżyńskiej oraz
        trzecie co do wielkości miasto województwa podlaskiego. Miasto
        królewskie Korony Królestwa Polskiego w województwie mazowieckim.
        Miejsce obrad sejmików ziemskich ziemi łomżyńskiej od XVI wieku do
        pierwszej połowy XVIII wieku. W latach 1975–1998 była stolicą
        województwa łomżyńskiego. Od 1 stycznia 1999 roku jest siedzibą powiatu
        łomżyńskiego. Od 28 października 1925 jest także stolicą diecezji
        łomżyńskiej Kościoła rzymskokatolickiego. Według danych zebranych przez
        przedstawicieli Głównego Urzędu Statystycznego z 30 czerwca 2020 roku,
        miasto liczyło 62 795 mieszkańców.
      </h2>
    </BackgroundImage>
  )
}

export default Article
