import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article9 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo3.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Sport</h1>
      <h2>
        Historia sportu w Łomży sięga końca XIX wieku, kiedy to w 1897 roku
        zorganizowano pierwsze wyścigi wioślarskie amatorów. Dwa lata później
        rozpoczęto starania o powołanie towarzystwa wioślarskiego, które to
        zainaugurowało swoją oficjalną działalność 26 stycznia 1902 roku pod
        nazwą Łomżyńskiego Towarzystwa Wioślarskiego. Stowarzyszenie prężnie
        działało do rozpoczęcia II wojny światowej. Jego działalność została
        zawieszona w latach 80. XX wieku. Kompleks rekreacyjno-sportowy „Orlik
        2012” 16 kwietnia 1926 roku został założony pierwszy klub piłkarski,
        obecnie trzecioligowy ŁKS Łomża. Ponadto w mieście działa kilkanaście
        klubów sportowych reprezentujących różne dyscypliny. Do najważniejszych
        sekcji oprócz piłki nożnej należą siatkówka, koszykówka, lekkoatletyka
        oraz sporty walki. Swoje największe sukcesy Łomżanie odnosili właśnie w
        lekkoatletyce, sportach walki, a także w kulturystyce. W
        czerwcu 2009 roku zorganizowano w Łomży Ogólnopolski Finał Gimnazjady w
        Piłce Nożnej Chłopców, w którym lokalna drużyna piłkarska z Publicznego
        Gimnazjum nr 8 zajęła 3. miejsce. Łomżyński sport wspierany jest
        przez Towarzystwo Krzewienia Kultury Fizycznej oraz Łomżyński Szkolny
        Związek Sportowy.
      </h2>
    </BackgroundImage>
  )
}

export default Article9
