import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article8 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo2.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Religia</h1>
      <h2>
        Mieszkańcy Łomży to głównie osoby wyznania rzymskokatolickiego, chociaż
        na przestrzeni wieków miasto było wielonarodowościowe i tym samym
        wielowyznaniowe. Oprócz katolików miasto zamieszkiwali m.in. wyznawcy
        judaizmu czy też protestanci, o czym świadczą zabytki architektury
        sakralnej oraz nagrobki na łomżyńskich cmentarzach. Dodatkowym
        potwierdzeniem wielowyznaniowej struktury miasta był fakt, że właśnie w
        Łomży 24 maja 1899 roku Józef Piłsudski dokonał konwersji na luteranizm,
        w czym uczestniczył łomżyński pastor ks. Mikulski. Oprócz osób
        wyznania rzymskokatolickiego w mieście mieszka również duża grupa
        muzułmanów pochodzenia czeczeńskiego. Osiem łomżyńskich parafii
        rzymskokatolickich skupionych jest w dwa dekanaty: Łomża – św. Brunona
        oraz Łomża – św. Michała Archanioła, które obejmują również dziewięć
        okolicznych miejscowości. Ponadto na terenie miasta działalność
        duszpasterską prowadzi Kościół Zielonoświątkowy w RP – zbór „Słowo
        Życia”, dwa zbory Świadków Jehowy (w tym grupa języka migowego oraz
        grupa rosyjskojęzyczna) oraz neopogański Rodzimy Kościół Polski.
      </h2>
    </BackgroundImage>
  )
}

export default Article8
