import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article4 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo2.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Transport Kolejowy</h1>
      <h2>
        Połączenia kolejowe z Łomżą były już planowane w XIX wieku, jednak
        dopiero z początkiem I wojny światowej i wkroczeniem Rosjan do miasta
        zostało otwarte połączenie Śniadowo – Łomża. Rosjanie rozpoczęli również
        przygotowywanie terenu pod tor kolejowy w kierunku Kolna, jednak w
        sierpniu 1915 opuścili miasto i prace zostały wstrzymane. Stworzenia
        połączenia pomiędzy Kolnem a Łomżą podjęli się również Niemcy, jednak
        bezowocnie. W latach 1922–1927 podejmowano kolejne próby stworzenia
        omawianego odcinka, jednak do budowy nigdy nie przystąpiono. W latach
        1917–1925 z Łomży do Warszawy kursowały trzy pociągi dziennie. Ponadto
        od 1921 roku otwarto kolej wąskotorową na odcinku Łomża – Nowogród,
        która była częścią trasy Myszyniec – Łomża. Przewozy pasażerskie drogą
        kolejową Łomża – Ostrołęka – Łomża funkcjonowały do 1 stycznia 1993
        roku. Trasą tą kursowało 5 par pociągów ze średnią prędkością 30 km/h, z
        której korzystało średnio rocznie 9510 osób – prawie 5 razy za mało, by
        utrzymać rentowność.
      </h2>
    </BackgroundImage>
  )
}

export default Article4
