import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article7 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo5.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Służby Mundurowe</h1>
      <h2>
        Łomżyńska Policja współpracuje z Żandarmerią Wojskową oraz korzysta z
        systemu monitoringu utworzonego przez miasto. Na terenie miasta znajduje
        się Komenda Miejska z siedzibą przy ul. Wojska Polskiego 9 i częścią
        sekcji przy ul. Partyzantów 48B. Łomżyńska komenda podlega Komendzie
        Wojewódzkiej. Dzieli się na 5 sekcji i 1 referat. Sekcja dzielnicowych
        dzieli miasto na dwa rewiry, czyli łącznie 13 rejonów. W mieście
        funkcjonuje także straż miejska. Od 1997 roku w mieście Łomża
        funkcjonuje program „Bezpieczne miasto” realizowany we współpracy z
        Komendą Miejską Policji, Strażą Miejską oraz innymi podmiotami życia
        społecznego. W ramach tego programu w 2001 roku uruchomiono miejski
        monitoring polegający na umieszczeniu kamer w miejscach wymagających
        stałego nadzoru. Za bezpieczeństwo pożarowe odpowiada Komenda
        Miejska Państwowej Straży Pożarnej mająca swą siedzibę przy ul.
        Sikorskiego 48/94, wspomagana przez Ochotniczą Straż Pożarną (10
        jednostek na terenie gminy Łomża).
      </h2>
    </BackgroundImage>
  )
}

export default Article7
