import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import { gsap } from "gsap"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const HeroWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;

  @media screen and (orientation: landscape) {
    justify-items: center;
  }
`

const HeroTitle = styled.h1`
  display: block;
  margin: 0;
  transform: translateY(-15vh);
  font-size: 8vh;

  @media screen and (orientation: landscape) {
    transform: translateY(-15vh) translateX(-30vw);
  }
`

const Hero = () => {
  const { herb } = useStaticQuery(graphql`
    query {
      herb: file(relativePath: { eq: "l1.png" }) {
        childImageSharp {
          gatsbyImageData(quality: 90, width: 800, webpOptions: { quality: 90 })
        }
      }
    }
  `)
  const Image = getImage(herb)
  const BgImage = convertToBgImage(Image)

  const Logo = React.useRef(null)

  React.useEffect(() => {
    const tl = gsap.timeline({ delay: 1 })
    const imageDiv = Logo.current.children[0]

    tl.to(imageDiv, 0.8, { autoAlpha: 1 })
  })
  return (
    <HeroWrapper ref={Logo}>
      <BackgroundImage
        Tag="div"
        className="herb"
        {...BgImage}
        preserveStackingContext
      ></BackgroundImage>
      <HeroTitle>Łomża</HeroTitle>
    </HeroWrapper>
  )
}

export default Hero
