import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article2 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo4.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Gospodarka</h1>
      <h2>
        Położenie Łomży w regionie o charakterze rolniczo-leśnym wskazuje na
        podstawowe kierunki rozwoju gospodarczego, którymi są branże
        nieuciążliwe ekologicznie, tj. przemysł spożywczy, browarnictwo,
        elektronika, przemysł materiałów budowlanych, drzewny, meblarski,
        produkcja i przetwórstwo płodów rolnych, a także turystyka i
        agroturystyka. Na terenie miasta nie istnieje żadne duże
        przedsiębiorstwo zatrudniające ponad 1000 pracowników, jednak
        funkcjonują przedsiębiorstwa, które znalazły się na Podlaskiej Setce
        Przedsiębiorców. Są to m.in. Browar Łomża 
        , DOMEL,
        FARGOTEX , KONRAD 
        , Łomżyńska Fabryka meblarski, PEPEES ,
        UniGlass Polska. Miasto jest siedzibą
        Podlaskiej centrali Agencji Restrukturyzacji i Modernizacji Rolnictwa.
        Na koniec 2007 roku liczba pracujących w mieście Łomża wynosiła 13 408,
        w tym 7170 kobiet, natomiast stopa bezrobocia na koniec września
        2012 roku wynosiła 15,8%. Liczba podmiotów gospodarczych
        zarejestrowanych w rejestrze REGON na koniec 2008 roku wynosiła 6421, w
        tym 6280 podmiotów sektora prywatnego. We wrześniu 2019 liczba
        zarejestrowanych bezrobotnych wynosiła 1800 osób, a stopa bezrobocia
        wynosiła 7,3%.
      </h2>
    </BackgroundImage>
  )
}

export default Article2
