import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article10 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo4.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Administracja</h1>
      <h2>
        Łomża jest miastem z prawami powiatu. Mieszkańcy Łomży wybierają do
        swojej rady miasta 23 radnych. Organem wykonawczym władz jest prezydent
        miasta. Siedzibą władz miejskich jest ratusz. Miasto jest także siedzibą
        starostwa powiatu łomżyńskiego, a także wiejskiej gminy Łomża. W Łomży
        znajduje się sąd rejonowy, sąd okręgowy, a także szereg urzędów rangi
        powiatowej. Mieszkańcy Łomży wybierają parlamentarzystów z okręgu
        wyborczego Białystok, a posłów do Parlamentu Europejskiego z okręgu
        wyborczego Olsztyn.W latach 1975–1998 Łomża była stolicą województwa
        łomżyńskiego. Województwo to było terenem rolniczym o trzeciej
        najniższej gęstości zaludnienia i jednym z najniższych wskaźników
        urbanizacji
      </h2>
    </BackgroundImage>
  )
}

export default Article10
