import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article6 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo4.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Ochrona Zdrowia</h1>
      <h2>
        Za ochronę zdrowia w powiecie łomżyńskim odpowiada prowadzony przez ten
        powiat i finansowany przez NFZ – Samodzielny Publiczny Zakład Opieki
        Zdrowotnej w Łomży z siedzibą przy ulicy Szosa Zambrowska 1/27. ZOZ
        składa się z izby przyjęć, pogotowia ratunkowego oraz czterech
        przychodni rejonowych. W Łomży mieści się również Szpital Wojewódzki
        im. Kardynała Stefana Wyszyńskiego. Uzupełnieniem publicznej służby
        zdrowia jest osiem Niepublicznych Zakładów Opieki Zdrowotnej oraz
        placówka terenowa służby zdrowia MSWiA. Poza tym na terenie miasta
        działają Caritas, przychodnie specjalistyczne (np. gabinety
        stomatologiczne) oraz 20 aptek. Natomiast usługi z zakresu
        rehabilitacji świadczy m.in. Centrum Rehabilitacji Caritas Diecezji
        Łomżyńskiej pw. św. Rocha.
      </h2>
    </BackgroundImage>
  )
}

export default Article6
