import * as React from "react"
import Hero from "../components/Hero"
import Article from "../components/Article"
import Article1 from "../components/Article1"
import Article2 from "../components/Article2"
import Article3 from "../components/Article3"
import Article4 from "../components/Article4"
import Article5 from "../components/Article5"
import Article6 from "../components/Article6"
import Article7 from "../components/Article7"
import Article8 from "../components/Article8"
import Article9 from "../components/Article9"
import Article10 from "../components/Article10"
import "../styles/index.css"
import { useStaticQuery, graphql } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import styled from "styled-components"

const Wrapper = styled.div`
  width: 100vw;
  height: 1100vh;
`

const IndexPage = () => {
  const { rynek } = useStaticQuery(graphql`
    query {
      rynek: file(relativePath: { eq: "tlo1.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(rynek)
  const Background = convertToBgImage(Image)
  return (
    <Wrapper>
      <BackgroundImage
        preserveStackingContext
        Tag="div"
        className="HeroImage"
        {...Background}
      >
        <Hero />
      </BackgroundImage>
      <Article />
      <Article1 />
      <Article2 />
      <Article3 />
      <Article4 />
      <Article5 />
      <Article6 />
      <Article7 />
      <Article8 />
      <Article9 />
      <Article10 />
    </Wrapper>
  )
}

export default IndexPage
