import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from "gatsby-background-image"
import "../styles/Hero.css"

const Article1 = () => {
  const { tlo2 } = useStaticQuery(graphql`
    query {
      tlo2: file(relativePath: { eq: "tlo3.png" }) {
        childImageSharp {
          gatsbyImageData(
            width: 2000
            quality: 90
            webpOptions: { quality: 85 }
          )
        }
      }
    }
  `)
  const Image = getImage(tlo2)
  const BgImage = convertToBgImage(Image)
  return (
    <BackgroundImage
      preserveStackingContext
      Tag="div"
      className="tlo2"
      {...BgImage}
    >
      <h1>Nazwa miasta</h1>
      <h2>
        Geneza nazwy Łomża nie jest jednoznaczna. Zygmunt Gloger pisze m.in. że:
        „Ludzie mieszkający w lasach i żyjący z myślistwa, pasterstwa i
        rybołówstwa nie mieli potrzeby budowania zamków, bo przyrodzoną i
        najbezpieczniejszą ich warownią były zawalone łomami starych drzew
        gąszcze leśne”. Natomiast według prof. Karola Zierhoffera nazwa (Łomża)
        oznaczała przypuszczalnie miejsce, gdzie był jakiś łom, jakieś
        pokruszone bloki kamienne bądź połamane przez wiatr drzewa. Nazwa miasta
        jest topograficzna, urobiona za pomocą przyrostka „ża” od tego samego
        rdzenia, co staropolskie łomić, łomać. Podobnie uformowana jest nazwa
        miasta Iłża. Nazwa Łomża występuje również w innych językach pod
        zmienionymi formami: łac. Lompza, niem. Lomscha, jid. Lomsza, ros.
        Ломжа, lit. Lomža. Jest to związane m.in. z wielonarodowościową
        strukturą etniczną miasta na przestrzeni dziejów i kontaktami handlowymi
        prowadzonymi przez mieszkańców miasta.
      </h2>
    </BackgroundImage>
  )
}

export default Article1
